body {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  font-family: 'IBM Plex Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  font-size: 2vmax;
}

.App {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  font-family: sans-serif;
  background-color:#ddd;
  background-position: 50% 50%;
  display:grid;
  grid-template-rows: 70%  30%;
}

h1 {
  font-size: clamp( 16px, 10vh, 250px);
  font-weight: 700;
  line-height: 100%;
  color: #1f2a44;
  text-align: center;
  width: 100%;
  padding: 0 2rem;
  z-index: 1;
}


article{height:100%; width: 100%; display:grid;
  align-items:center; justify-content:middle}
  article > img { max-width: 100%; max-height: 100%;}

  @media (orientation: portrait) {
  article > img { height: auto;}
  }



  .comment {
    width: 100%;
    line-height:1.5em;
    justify-self: end;
    align-items: space-between;
    text-align: left;

    background-color: white;
    padding: 1.3em;
    font-size: clamp(1rem, 3vw, 3rem);
    box-shadow: inset 0 0 0 .5em #fff, inset 0 0 0 .6em red;
    min-height: 5em;
    display: grid;
    align-items: center;

    grid-template-columns: 6em  auto;
    grid-auto-columns: 1 3;

  }

  .comment img {

    display: block;
    width: 5em;
    height: auto;
    max-width: 20vh;
    align-self: left;
    border-radius: 50%;
  }

  div {
   justify-self:start;
  }

  h4{margin:0}

  @media (orientation: landscape) {
    article > img { width: auto; height: 100%;  display:block; margin: 0 auto }
    .comment{font-size: 4vh}
  }
